    "\"rows\": 2,\n" +
    "\"cols\": 2,\n" +
    "\"fixed\": {},\n" +
    "\"topfixed\": {},\n" +
    "\"bottom_solution\": [0, 1],\n" +
    "\"right_solution\": [2, 0],\n" +
    "\"maxtilts\": 0,\n" +
    "\"colormap\": {\"0\": \"none\", \"1\": \"red\", \"2\": \"blue\"}\n" +
    "}";

class Board extends React.Component {
    constructor(props) {
        super(props);
        try {
            let data = JSON.parse(document.getElementById("input").value);
            console.log(data);
            assert("rows" in data, "rows missing");
            assert("cols" in data, "cols missing");
            assert("fixed" in data, "fixed missing");
            assert("topfixed" in data, "topfixed missing");
            assert("bottom_solution" in data, "bottom_solution missing");
            assert("right_solution" in data, "right_solution missing");
            assert("maxtilts" in data, "maxtilts missing");
            assert("colormap" in data, "colormap missing");
            assert(data.rows === data.right_solution.length, "rows and right_solution length mismatch");
            assert(data.cols === data.bottom_solution.length, "cols and bottom_solution length mismatch");
            assert(Object.keys(data.colormap).length >= 2, "too few colors");

            let matrix = Array(data.rows).fill().map(() => Array(data.cols).fill(0));

            console.log(matrix);
            for (let row in data.fixed) {
                for (let col in data.fixed[row]) {
                    matrix[parseInt(row)][parseInt(col)] = parseInt(data.fixed[row][col]);
                }
            }

            let top = Array(data.cols).fill(0);

            for (let col in data.topfixed) {
                top[col] = parseInt(data.topfixed[col]);
            }

            let right = Array(data.rows).fill(0);
            let bottom = Array(data.cols).fill(0);

            let f = Array(data.rows).fill().map(() =>(Array(data.cols).fill().map(() => [0, 0, 0, 0])))

            let colorNums = Object.keys(data.colormap);
            let maxSources = Array(Math.max(...colorNums)+1).fill().map(() => 0);

            for (let i=0; i<data.bottom_solution.length; i++) {
                maxSources[data.bottom_solution[i]] += 1;
            }
            console.log("bot", maxSources);
            for (let i=0; i<data.right_solution.length; i++) {
                maxSources[data.right_solution[i]] += 1;
            }

            console.log("both", maxSources);

            this.state = {
                data: data,
                matrix: matrix,
                top: top,
                right: right,
                bottom: bottom,
                flows: f,
                tilts: 0,
                maxSources: maxSources,
                currSources: maxSources.map(x => 0),
            }

        }
        catch (err) {
            this.state = {
                error: err
            }
            console.log("error: " + err);
            console.log(err.stack);
        }
    }

    topClick(i) {
        console.log(this.state.top);
        let t = this.state.top;
        if (i in this.state.data.topfixed) {
            this.process();
            return;
        }
        let arr = Object.keys(this.state.data.colormap).map(key => parseInt(key));
        let oldv = t[i];
        t[i] = (t[i] + 1) % (1+ Math.max(...arr));
        let newv = t[i];
        if (oldv != 0) {
            this.state.currSources[oldv] -= 1;
        }
        if (newv != 0) {
            this.state.currSources[newv] += 1;
        }
        this.state.top = t;
        this.forceUpdate();
        this.process();
    }

    bodyClick(i, j) {
        let m = this.state.matrix;
        if (i in this.state.data.fixed && j in this.state.data.fixed[i]) {
            return;
        }
        console.log(i, j)
        m[i][j] = (m[i][j] + 1) % 2;
        if (m[i][j] === 0) {
            this.state.tilts -= 1;
        } else {
            this.state.tilts += 1;
        }
        this.state.matrix = m;

        this.forceUpdate();
        this.process();
    }

    process() {
        let f = Array(this.state.data.rows).fill().map(() => Array(this.state.data.cols).fill().map(() => [0, 0, 0, 0]));
        let curr = this.state.top.map(x => x);

        let r = Array(this.state.data.rows).fill(0);
        for (let i = 0; i < this.state.data.rows; i++) {
            let l = 0;
            for (let j = 0; j < this.state.data.cols; j++) {
                if (this.state.matrix[i][j] === 1 || curr[j] < l) {
                    let t_temp = parseInt(curr[j]);
                    let l_temp = l;
                    curr[j] = l;
                    l = t_temp;
                    f[i][j] = [t_temp, curr[j], l_temp, l]; //u, d, l, r
                } else {
                    f[i][j] = [curr[j], curr[j], l, l];
                }
            }
            r[i] = l;
        }
        this.state.bottom = curr;
        this.state.right = r;
        this.state.flows = f;
    }

    #getFlowImages() {
        let u, l, d, r;
        let images = Array(this.state.data.rows).fill().map(() => Array(this.state.data.cols).fill().map(() => [0, 0]));
        for (let i = 0; i < this.state.data.rows; i++) {
            for (let j = 0; j < this.state.data.cols; j++) {
                [u, d, l, r] = this.state.flows[i][j];
                if (u===r) {
                    images[i][j][0] = uMap[this.state.data.colormap[u]];
                    images[i][j][1] = dMap[this.state.data.colormap[l]];
                } else {
                    images[i][j][0] = vMap[this.state.data.colormap[u]];
                    images[i][j][1] = hMap[this.state.data.colormap[l]];
                }
            }
        }
        return images;

    }

    render() {
        if (this.state.error) {
            return (
                <div>
                    <h1>{this.state.error.toString()}</h1>
                </div>
            )
        }
        this.process();
        let f = this.#getFlowImages();
        const cellw = parseInt(50 / (this.state.data.rows + 3)) + "vw";
        let myMap = this.state.data.colormap;
        let rem = this.state.maxSources.length + 1.5
        return <div id="wrapper">
            <div id="board">
                <table>
                    <tbody>
                    <tr>
                        {this.state.top.map((col, colind) =>
                            <td onClick={() => this.topClick(colind)} style={{aspectRatio: 1, width: cellw, background: (colind in this.state.data.topfixed)?"gray":"none"}}>
                                <img src={TapMap[myMap[col]]}/>
                            </td>
                        )}
                    </tr>
                    {this.state.matrix.map((row, rowind) =>
                        <tr>
                            {row.map((cell, colind) =>
                                <td className="grid" onClick={() => this.bodyClick(rowind, colind)} style={{aspectRatio: 1 / 1, width: cellw}}>
                                    <img className="background" src={TileMap[this.state.matrix[rowind][colind]]} style={{background: (rowind in this.state.data.fixed && colind in this.state.data.fixed[rowind])?"gray":"none"}}/>
                                    <img className="flow1" src={f[rowind][colind][0]} />
                                    <img className="flow2" src={f[rowind][colind][1]} />
                                </td>
                            )}
                            <td style={{aspectRatio: 1 / 1, width: cellw}}>
                                {console.log(myMap[this.state.data.right_solution[rowind]])}
                                <img src={BucketMap[myMap[this.state.data.right_solution[rowind]]]} style={{background: TranslucentMap[myMap[this.state.right[rowind]]], width: cellw}}/>
                            </td>
                        </tr>
                    )}
                    <tr>
                        {this.state.bottom.map((col, colind) =>
                            <td className="out" style={{background: TranslucentMap[myMap[col]]}}>
                                <img src={BucketMap[myMap[this.state.data.bottom_solution[colind]]]}/>
                            </td>
                        )}
                        <td></td>
                    </tr>
                    </tbody>
                </table>
            </div>
            <div id="bottom">
                <div id="tiles">
                    <div className="tile" style={{background: this.state.data.maxtilts - this.state.tilts >= 0?"none":"red"}}>
                        <img src={waterfalltilt} style={{"width": parseInt(128/rem) + "px", "height": parseInt(128/rem) + "px"}}/>
                        <p>{this.state.data.maxtilts - this.state.tilts}</p>
                    </div>
                    {this.state.maxSources.slice(1).map((col, colind) =>
                        <div className="tile" style={{background: col - this.state.currSources[colind+1] >= 0?"none":"red"}}>
                            <img src={TapMap[myMap[colind+1]]} style={{"width": parseInt(128/rem) + "px", "height": parseInt(128/rem) + "px"}}/>
                            <p>{col - this.state.currSources[colind+1]}</p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    }
}
function get_cookie() {
    let d = document.cookie;
    let c = d.split(";");
    let cookie = {};
    for (let i = 0; i < c.length; i++) {
        let t = c[i].split("=");
        cookie[t[0]] = decodeURIComponent(t[1]);
    }
    if ("input" in cookie) {
        return cookie["input"];
    }
    return "";
}

const Input = () => {
    if (document.cookie !== "") {
        val = get_cookie();
    }
    return <div>
        <textarea id="input" rows="15" cols="50" defaultValue={val}/>
        <button onClick={() => setBoard()}>Generate Level</button>
        <button onClick={() => Reset()}>Reset</button>
    </div>
}

const Reset = () => {
    document.cookie = "input="+encodeURIComponent(defaultVal);
    window.location.reload();
}

function setBoard() {
    let res = document.getElementById("input").value;
    console.log(res);
    document.cookie = "input=" + encodeURIComponent(res);
    console.log("cookie", "input="+res);
    console.log(document.cookie);
    root.render(
        <Board />
    );
}

let root = ReactDOM.createRoot(document.getElementById("root"));
root.render(<div id="wrapper">
    <Input />
</div>)
